
.faqs{
  margin-top: 15px;
}
.faqs .item{
  border-bottom: 1px solid #eee;
  padding-bottom: 16px;
  margin-bottom: 16px;
}
.faqs .item .issue{
  color: #56bdf8;
  margin-bottom: 16px;
  font-size: 1.1rem;
  font-weight: 700;
}
.faqs .item .reply{
  background: #f6f6f6;
  padding: 20px;
  line-height: 1.8;
}

.pay-wrp{
  width: 100%;
  height: 100%;
  position: fixed;
  left: 0;
  top: 0;
  background: RGBA(248, 247, 247, 1.00)
}
.pay{
  background: #FFF;
  border-radius: 20px;
  width: 90%;
  margin: 30% auto 0;
  position: relative;
  -webkit-box-shadow: 0 0 4px 2px RGBA(0, 0, 0, 0.3);
          box-shadow: 0 0 4px 2px RGBA(0, 0, 0, 0.3);
  text-align: center;
}
.pay-icon {
  background: url(/helpin/static/img/icon-pay.0b96b70.png) center no-repeat/100%;
  position: absolute;
  left: 50%;
  top: -50px;
  width: 100px;
  height: 100px;
  margin-left: -50px;
  border-radius: 50%;
}
.money {
  color: RGBA(91, 91, 91, 1.00);
  font-size: 5rem;
  padding: 25% 0 5%;
}
.topay{
  display: block;
  font-size: 1.2rem;
  line-height: 1;
  color: #FFF;
  background: RGBA(46, 188, 131, 1.00);
  border-radius: 10px;
  padding: 15px 0;
  margin: 10% 50px 10%;
  font-size: 20px;
}
.footer{
  border-top: 2px dotted RGBA(46, 188, 131, 1.00);
  position: relative;
  padding: 12% 0;
  color: #888;
  line-height: 1;
  font-size: 14px;
}
.footer::before,.footer::after{
  /* content: ''; */
  position: absolute;
  top: -8px;
  width: 15px;
  height: 15px;
  background: RGBA(46, 188, 131, 1.00);
  border-radius: 50%;
}
.footer::before{
  left: -8px;
}
.footer::after{
  right: -8px;
}
.pay.success .pay-icon{
  background-image: url(/helpin/static/img/icon-pay-success.a9dfab6.png);
}
.pay.success .topay{
  background: RGBA(255, 162, 4, 1.00);
}
.pay.success .footer{
  border-top-color: RGBA(255, 162, 4, 1.00);
}
.pay.error .pay-icon{
  background-image: url(/helpin/static/img/icon-pay-error.bab5f6b.png);
}
.pay.error .topay{
  background: RGBA(255, 83, 82, 1.00);
}
.pay.error .footer{
  border-top-color: RGBA(255, 83, 82, 1.00);
}
